let trava = false;
let iCount1, iCount2, iTexto;
/*
iCount1 --> Comprimento da mscara no evento onkeydown (Referncia para filtrar teclas de aderncia!!!);
iCount2 --> Comprimento da mscara no evento onkeyup;
iTexto --> Texto atual na mscara, antes da funo mascara() ser chamada com onkeyup;
nChar --> Varivel controle;
*/

//Funo chamada a partir do evento onkeydown;
export const MaskDown = e => {
  if (e != undefined) {
    if (trava == false) {
      //Armazenando a frase em uma varivel, para ser utilizada durante o backspace;
      iTexto = e.value;

      //Comprimento da mscara no evento onkeydown;
      iCount1 = e.value.length;
      //alert(iCount1);
      //Adicionando trava para evitar possvel tecla de aderncia:
      trava = true;
    }
  }
};

//Funo chamada a partir do evento onkeyup;
export const MaskUp = (e, msc) => {
  if (e != undefined) {
    //Comprimento da mscara no evento onkeyup;
    iCount2 = e.value.length;
    //Verificando se o usurio inseriu novos caracteres, ou se apertou a tecla DELETE/BACKSPACE:
    // -------------------- Caso 1: Insero de novos caracteres --------------------
    //Quantidade caracteres final (onkeydown) maior que a inicial (onkeyup) -> Houve insero de caracteres;
    if (iCount2 > iCount1) {
      //Pequena correo para bug que estava ocorrendo com navegao com tecla TAB;
      //A tecla TAB no dispara nem keydown, fazendo com que iCount1 seja 0 e iCount2 o valor normal;
      //Por isso, vamos corrigir (Agradecimentos ao  -rafael- pela observao do bug) :-)
      if (iCount2 - iCount1 > 1) {
        iCount1 = iCount2 - 1;
        e.select;
      } else {
        //Recortando a mscara para filtrar possvel tecla de aderncia:
        e.value = e.value.substr(0, iCount1 + 1);
        //Verificando se o comprimento da mscara no  excedido;
        if (e.value.length > msc.length) {
          e.value = e.value.substr(0, msc.length);
        }
        //Antes de mais nada, verificar se a mscara j inicia com caracteres especiais e j inser-los;
        if (iCount1 == 0) {
          //Contando o nmero de caracteres especiais (se porventura existirem):
          if (msc.substring(iCount1, iCount1 + 1) != '#') {
            nChar = 1;
            //Contar as ocorrncias de smbolos especiais, diferentes de: #, que iniciam a mscara:
            while (
              msc.substring(iCount1 + nChar, iCount1 + nChar + 1) != '#' &&
              nChar <= msc.length
            ) {
              nChar++;
            }
            e.value =
              msc.substring(0, iCount1 + nChar) +
              e.value.substr(0, iCount1 + 1);
          }
          //Se no iniciar com caracteres especiais:
        } else {
          //Verificando se existe caracteres especiais  frente do ltimo caractere inserido:
          if (msc.substring(iCount1 + 1, iCount1 + 2) != '#') {
            var nChar = 1;
            //Contar as ocorrncias de smbolos especiais que sucedem o ltimo caractere inserido;
            while (
              msc.substring(iCount1 + nChar, iCount1 + nChar + 1) != '#' &&
              nChar <= msc.length
            ) {
              nChar++;
            }
            e.value =
              e.value.substr(0, iCount1 + 1) +
              msc.substring(iCount1 + 1, iCount1 + nChar);
          }
        }
      }
      // -------------------- Caso 2: Deleo de caracteres | Backspace --------------------
    } else {
      //Verificando se o caractere apagado  um smbolo especial ou no:
      if (msc.substr(iCount2, 1) != '#') {
        nChar = 1;
        //alert(msc.substr(iCount2,1));
        //Looping para percorrer os caracteres especiais retrocedentes:
        while (msc.substr(iCount1 - nChar, 1) != '#' && nChar <= iCount1) {
          alert(msc.substr(iCount1 - nChar, 1) + ' -- ' + nChar);
          nChar++;
        }
        //Apagando os caracteres retrocedentes, conforme o valor atribudo a nChar:
        e.value = iTexto.substr(0, iCount2 - nChar + 1);
      } /*else {
	//Recortando a mscara para filtrar possvel tecla de aderncia: Default: desabilitado.
	objCampoMascara.value = iTexto.substr(0,iCount1-1);
	}*/
    }
    //Liberando a trava para possibilitar novo evento onkeydown:
    trava = false;
  }
};
